import alForm from './alForm/index'
import alFormItem from './alFormItem/index'
import alInput from './alInput/index'
import '../examples/styles/al-form.css'
// 
const components = [alForm, alFormItem, alInput];
//  install  Vue  use 
const install = function(Vue) {
  // 
  if (install.installed) return;
  // 
  components.forEach(component => {
    Vue.component(component.name, component)
  });
};
// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
export default {
  //  install Vue.use() 
  install,
  // 
  alForm,
  alFormItem,
  alInput
};